<template>
  <div class="switch-holder" v-bind:ref="uniqueRef">
		<div class="switch-name">{{param.key}}</div>
		<div v-on:click=change() class="switch-value">{{param.values[currentIndex]}}</div>
  </div>
</template>
<script>
import { bus } from '../main';

export default {
  name: "DiscreteSwitch",
  props:[
    'uniqueRef', 'param', 'source', 'inkeys'
	],
	data:function(){
		return {
			currentIndex: this.param.defaultIndex
		}
	},
  methods:{
		change:function(){
			console.log('change')
			let changedIndex = this.currentIndex + 1;
			if(changedIndex < this.param.values.length) {
				this.currentIndex = changedIndex;
			} else {
				this.currentIndex = 0;
			}
			console.log(this.param.values[this.currentIndex]);
			this.emitUpdate()
		},
		emitUpdate(){
			let value = {};
			let _child = value;
			if(this.inkeys){
				this.inkeys.forEach(k => {
					_child[k] = {};
					_child = _child[k]
				});
			}
			_child[this.param.key] = this.param.values[this.currentIndex];
			console.log(JSON.stringify(value));
			bus.$emit('update-param', {
				source: this.source,
				value: value
			});
		}
  },
  mounted(){}
}
</script>
<style scoped>
.switch-holder {
  margin:2px;
  padding:4px;
	display:flex;
	flex-direction:row;
	align-items:center;
}
.switch-name{
	flex-grow:1;
	font-size:0.7em;
	text-align:left;
}
.switch-value{
	flex-grow:2;
	border:black 1px dotted;
	background-color:rgba(0,0,0, 0.1);
}
</style>