<template>
  <div class="m-holder" ref="mh">
    <div class="button-holder" v-on:click="toggleOverlayVisible()">
      <span v-if=!overlayIsVisible class="icon material-icons not-selectable">settings</span>
      <span v-if=overlayIsVisible class="icon material-icons not-selectable">close</span>
    </div>
    <div class="overlay" v-bind:class={hidden:!overlayIsVisible}>
      <div class="content-title">
        <h2>SETTINGS</h2>
      </div>
      <div class="content-scroller">
        <h3>Synth</h3> 
        <div class="synth-row">
          <div class="synth-cell" v-bind:class="{'synth-selected':synth.isSelected}" v-for="(synth, iS) of synths" v-bind:key="iS"
                                    v-on:click="loadSynth(iS)">
              <span class="synth-name">{{synth.name}}</span> 
          </div>
        </div>
        <template v-if="isReady">
          <div v-for="(synth, iS) in synths" v-bind:key="'S-' + iS">
            <Synth v-if="iS===synthIndex" v-bind:properties="synth"/>
          </div>
          <h3>Effects</h3>
          <div v-for="(effect, iE) in effects" v-bind:key="'E-' + iE">
            <Effect v-bind:properties="effect" v-bind:source="getEffectSource(iE)"/>
          </div>
        </template>
      </div>
    </div>
    <h1>Music Maker</h1>
    <div v-bind:class="{'grid-row':isLandscape, 'grid-column':!isLandscape}">
      <div v-bind:class="{'grid-column':isLandscape, 'grid-row':!isLandscape}">
        <div class="grid-header cell"></div>
        <div class="grid-header cell" v-for="(pattern, iPH) of patterns" v-bind:key="iPH">{{pattern.name}}</div>
        <div class="grid-header cell"></div>
      </div>
      <div v-bind:class="{'grid-column':isLandscape, 'grid-row':!isLandscape}" v-for="(root, iR) of roots" v-bind:key="iR">
        <div class="grid-header cell">{{root}}</div>
        <div class="grid-cell cell glowing-shadow not-selectable" v-for="(pattern, iP) of patterns" v-bind:key="iP" v-on:click="play($event, iR, iP)"></div>
        <div class="grid-header cell">{{root}}</div>
      </div>
      <div v-bind:class="{'grid-column':isLandscape, 'grid-row':!isLandscape}">
        <div class="grid-header cell"></div>
        <div class="grid-header cell" v-for="(pattern, iPHb) of patterns" v-bind:key="iPHb">{{pattern.name}}</div>
        <div class="grid-header cell"></div>
      </div>
    </div>
  </div>
</template>

<script>
import Effect from "../components/Effect.vue";
import Synth from "../components/Synth.vue";
import Tone from "tone";
import { synths, effects } from "../extras/params.js";
import { bus } from '../main';
export default {
  name: "Music",
  components: {
    Effect, Synth
  },
  props: {},
  data: function(){
    return {
      hasStarted:false,
      isReady:false,
      isLandscape:false,
      overlayIsVisible:false,
      synth:null,
      synths:null,
      effects:null,
      patterns:[
        { name: 'min9', intervals:[-24,-12,2,3,7,10] },
        { name: 'maj9', intervals:[-24,-12,2,4,7,11] },
        { name: '7#9', intervals:[-24,-12,3,4,8,10,13]},
        { name: '11', intervals:[-24,-12,5,10,14] }
      ],
      roots:['C','Db','D','Eb','E','F','Gb','G','Ab','A','Bb','B']
    }
  },
  computed:{},
  methods:{
    getEffectSource(index){
      return 'effect-' + index;
    },
    toggleOverlayVisible:function(){
      this.overlayIsVisible = !this.overlayIsVisible;
    },
    play:function(e, rootIndex, patternIndex){
      e.preventDefault();
      let nOctave = 4;
      let rootNote = this.roots[rootIndex] + nOctave;
      let intervals = this.patterns[patternIndex].intervals;
      let rootFrequency = new Tone.Frequency(rootNote);
      let chord = rootFrequency.harmonize(intervals);
      this.synth.triggerAttackRelease(chord, "8n");
      bus.$emit('chord-played', { rootNote: this.roots[rootIndex], 
                                  intervals:this.patterns[patternIndex].intervals,
                                  x: e.clientX,
                                  y: e.clientY})
    },
    loadSynth: function(synthIndex){
      //if(!this.synth) this.synth.disconnect(); // TODO: Or should it be dispose()?
      if(!this.synth || this.synthIndex !== synthIndex){
        this.synthIndex = synthIndex;
        this.synth = new Tone.PolySynth(7, this.synths[synthIndex].o);
      } else {
        this.synth.disconnect();
      }
      this.synth.connect(this.postSynthNode);


      this.synths.forEach((synth, index) => {
        synth.isSelected = false;
        if(index === synthIndex) synth.isSelected = true;
      });
    },
    loadSynths: function(){
      synths.forEach(synth => {
        synth.name = synth.objectKey;
        synth.isSelected = false;
        synth.o = Tone[synth.objectKey];
        //This might not work on some browsers
        synth.params.unshift({isDiscrete:false, key:'volume', min:-100, max:100, default:0})
      });
      this.synths = synths;
    },
    toggleEffect: function(index){
      let effect = this.effects[index];
      effect.isConnected = !effect.isConnected;
      this.updateEffectsChain();
    },
    loadEffects: function(){
      effects.forEach((effect, iE) => {
        effect.index = iE;
        effect.name = effect.objectKey; //TODO: May need to remove this.
        effect.isConnected = false;
        effect.params.forEach((param, iP) => {
          param.name = param.key; //TODO: May need to remove this.
          param.index = iP;
          param.effectIndex = iE;
        });
        effect.o = new Tone[effect.objectKey]; // Note 'new'
      });
      this.effects = effects;
      
    },
    updateEffectsChain: function(){
      let nextNode = this.Tone.Master;
      this.effects.forEach(effect => {
        if(effect.isConnected) {
          let thisNode = effect.o;
          thisNode.connect(nextNode);
          nextNode = thisNode;
        } else {
          effect.o.disconnect();
        }
      });
      this.postSynthNode = nextNode;
      this.loadSynth(this.synthIndex);
    }
  },
  mounted(){
    let width = this.$refs.mh.offsetWidth;
    let height = this.$refs.mh.offsetHeight;
    this.isLandscape = width > height;
    console.log(this.isLandscape);
    this.Tone = Tone;
    this.Tone.context.lookAhead = 0; // This reduces latency.
    this.loadSynths();
    this.synthIndex = 0;
    this.loadEffects();
    this.updateEffectsChain();
    this.isReady = true;

    bus.$on('update-chain', () => {
      this.updateEffectsChain();
    });
    bus.$on('update-param', d => {
      if(d.source === 'synth'){
        this.synth.set(d.value);
      }
    });
  }
};
</script>

