<template>
  <div class="effect-holder">
    <div class="top-section">
    <div class="effect-name">{{properties.name}}</div>
    <div class="on-off" v-on:click="toggleConnected()">
      <div class="on" v-if=isConnected>ON</div>
      <div class="off" v-if=!isConnected>OFF</div>
    </div>
    </div>
    <ParamGroup v-bind:params="properties.params" v-bind:inkeys="inkeys" v-bind:source="source"/>
  </div>
</template>
<script>
import ParamGroup from '../components/ParamGroup.vue'
import { bus } from '../main';

export default {
  name: "Effect",
  components:{
    ParamGroup
  },
  props:[
    'properties', 'source'
  ],
  data:function(){
    return {
      isConnected: this.properties.isConnected,
      inkeys:[]
    }
  },
  methods:{
    toggleConnected(){
      this.properties.isConnected = !this.properties.isConnected;
      this.isConnected = this.properties.isConnected;
      bus.$emit('update-chain');
    }
  },
  mounted(){
    bus.$on('update-param', d => {
      if(d.source === this.source){
        this.properties.o.set(d.value);
      }
    })
  }
}
</script>
<style scoped>
.effect-holder {
  background-color:ivory;
  margin:20px;
  padding:10px;
  border-radius:0.5em;
}
.top-section{
  display:flex;
  flex-direction:row;
  justify-content:space-between;
  align-items:center;
}
.effect-name{
  font-weight:bold;
  font-size:1.2em;
}
.on-off{
  font-size:0.8em;
}
.on{
  background-color:rgba(0,255,0,0.5);
  color:white;
  font-weight:bolder;
  width:3em;
  border-radius:1em;
  border:0.1em dotted green;
}
.off{
  background-color:rgba(0,0,0,0.2);
  color:gray;
  width:3em;
  border-radius:1em;
  border:0.1em dotted black;
}
</style>