<template>
  <div class="pg-holder">
    <div v-for="(param, i) of params" v-bind:key="param.key + i">
      <template v-if="'params' in param">
				<div class="group-title">{{param.key}}</div>
				<ParamGroup v-bind:params="param.params" 
										v-bind:inkeys="getInkeys(param.key)"
										v-bind:source="source"></ParamGroup>
      </template>
			<template v-else>
        <Slider v-if="!param.isDiscrete"
								v-bind:uniqueRef="getUniqueId()" 
								v-bind:param="param" 
								v-bind:inkeys="inkeys"
								v-bind:source="source"/>
        <DiscreteSwitch v-if="param.isDiscrete" 
						v-bind:param="param"
						v-bind:inkeys="inkeys"
						v-bind:source="source"/>
      </template>
    </div>
  </div>
</template>
<script>
import Slider from '../components/Slider.vue'
import DiscreteSwitch from '../components/DiscreteSwitch.vue'
import shortid from 'shortid';
//import { bus } from '../main';

export default {
  name: "ParamGroup",
  components:{
    Slider, DiscreteSwitch
  },
  props:[
    'params','inkeys','source'
	],
  methods:{
		getInkeys(key){
			//if(!this.inkeys) this.inkeys = [];
			let _inkeys = this.inkeys.slice();
			_inkeys.push(key);
			return _inkeys;
		},
		getUniqueId(){
			let id = shortid.generate();
			return id;
		}
	},
  mounted(){
		//console.log(this.source);
	}
}
</script>
<style scoped>
.pg-holder {
  padding:0.5em;
}
.group-title {
	font-weight:bold;
	font-size:0.9em;
	text-align:left;
}
</style>