<template>
  <div class="sholder">
    <h3>{{properties.name}}</h3>
    <ParamGroup v-bind:params="properties.params" v-bind:inkeys="inkeys" v-bind:source="source"/>
  </div>
</template>
<script>
import ParamGroup from '../components/ParamGroup.vue'
import { bus } from '../main';

export default {
  name: "Synth",
  components:{
    ParamGroup
  },
  data:function(){
    return{
      inkeys:[],
      source:'synth'
    }
  },
  props:[
    'properties'
  ],
  methods:{
    toggleConnected(){
      this.properties.isConnected = !this.properties.isConnected;
      this.isConnected = this.properties.isConnected;
      bus.$emit('update-chain');
    }
  },
  mounted(){
  }
}
</script>
<style scoped>
.sholder {
  margin:20px;
  padding:10px;
  background-color:yellow;
  border-radius:1em;
}
</style>