<template>
    <div id="art-holder" ref="a_h"></div>
</template>
<script>
import { bus } from '../main';
import Snap from 'imports-loader?this=>window,fix=>module.exports=0!snapsvg/dist/snap.svg.js'
import { gsap } from 'gsap';
export default {
  name: "Art",
  props: {},
  methods:{

  },
  mounted(){
    let parent = this.$refs.a_h;
    let svg = document.createElementNS("http://www.w3.org/2000/svg", "svg");
    svg.setAttribute('style', 'border: 1px solid black');
    svg.setAttribute('width', parent.offsetWidth,);
    svg.setAttribute('height', parent.offsetHeight);
    svg.setAttributeNS("http://www.w3.org/2000/xmlns/", "xmlns:xlink", "http://www.w3.org/1999/xlink");
    parent.appendChild(svg);
    let draw = Snap(svg);
    bus.$on('chord-played', chord => { 
      let colors = ["#A8E6CE", "#DCEDC2", "#FFD3B5",  "#FFAAA6",  "#FF8C94"];
      let color = colors[Math.floor(Math.random() * 6)];
      let r = draw.circle(chord.x, chord.y, 0).attr('fill', color);
      //This is still buggy. Sometimes circle isn't recognized by gsap.
      if(typeof r !== "string") gsap.to(r.node, {r:300, opacity: 0, 
                                                 duration:3, ease:'power2.out'})
    });
  }
}
</script>
<style>
#art-holder{
  width:100vw;
  height:100vh;
}
</style>